i.icon-play {
  width: 50px;
  height: 50px;
  display: flex;
  position: relative;
  background: #fff;
  border-radius: 50%;
}

i.icon-play::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 60%;
  transform: translate(-50%, -50%);
  border: 7px solid transparent;
  border-left: 12px solid #53BF51;
}

.play-video {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 180px;
  width: 110px;
}

.play-video i {
  background: #53BF51;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.play-video i:before {
  border-left: 12px solid #fff;
  z-index: 2;
}

.play-video i span {
  position: absolute;
  border-radius: 50%;
  border: 1px solid #53BF51;
  animation: pulse 3s linear infinite;
  width: calc(100% + 40px);
  height: calc(100% + 40px);
  transition: all 0.5s;
  z-index: 1;
}

.play-video i span:nth-child(2) {
  animation-delay: .7s;
}

.play-video i span:nth-child(3) {
  animation-delay: 1.5s;
}

.play-video i span:nth-child(4) {
  animation-delay: 2.3s;
}

.play-video:hover span {
  animation-play-state: paused;
}

.play-video:hover b {
  text-decoration: underline;
}

.play-video b {
  position: absolute;
  bottom: 0px;
  display: block;
  width: 90px;
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: #53BF51;
}

@keyframes pulse {
  0% {
    transform: scale(0.5);
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

